.nav-bar {
  background-color: $nav-bar--bg;
  padding: $nav-bar--padding;
  font-size: rem(16);
  &--center {
    display: flex;
    justify-content: center;
  }
  &--end {
    display: flex;
    justify-content: flex-end;
  }
  &--sm {
    font-size: rem(14);
  }
}
.nav-bar__menu {
  padding: 0;
  list-style: none;
  margin-bottom: 0;
  display: flex;
  a {
    &:hover {
      color: #fff;
      text-decoration: none;
    }
  }
  // margin-#{$start}: #{-$nav-bar-space-between};
  // margin-#{$end}: #{-$nav-bar-space-between};
}

.nav-bar__menu-item {
  padding-#{$start}: $nav-bar-space-between;
  padding-#{$end}: $nav-bar-space-between;
  position: relative;
  > .d-flex {
    svg {
      width: 24px;
      height: 24px;
    }
  }
}

.nav-bar__menu-item--flex {
  align-items: center;
  font-size: 20px;
}

.nav-bar__menu-icon {
  padding: 5px;
  background-color: $color-secondary--dark;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-#{$end}: 10px;
  svg {
    margin: 0;
  }
}

.nav-bar__menu-icon--light {
  svg {
    fill: #fff;
  }
}

.nav-bar__login {
}
.nav-bar__primary-menu {
  font-size: rem(18);
  border-top: 1px solid rgba(#fff, 0.2);
  margin-top: 10px;
  padding-top: 10px;
  padding-#{$start}: 20px;
  .nav-bar__menu-item {
    &:not(:last-child) {
      @include breakpoint(large) {
        padding-#{$end}: 40px;
      }
    }
    &:first-child {
      overflow: hidden;
      border-radius: 5px 5px 0 0;
    }
    &:last-child {
      overflow: hidden;
      border-radius: 0 0 5px 5px;
    }
  }
  .nav-bar__sub-menu {
    .nav-bar__menu-item {
      padding: 0;
    }
  }
}
.nav-bar__menu-link {
  color: $menu-link-color;
  padding-#{$start}: $nav-bar-space-between;
  padding-#{$end}: $nav-bar-space-between;
  display: flex;
  align-items: center;
  @include transition();
  &:hover {
    color: $menu-link-hover;
    text-decoration: none;
  }
}
.nav-bar__secondary-menu {
  .nav-bar__menu-item {
    display: flex;
    align-items: center;
    padding-right: 0;
    padding-left: 0;
    &:not(.nav-bar__tablet) {
      @include breakpoint(isTablet) {
        display: none;
      }
    }
  }
  .nav-bar__menu-item + .nav-bar__menu-item {
    &:before {
      content: '';
      display: inline-flex;
      width: 1px;
      height: 25px;
      margin: 0 25px;
      background: rgba(0, 0, 0, 0.3);
    }
  }
}
.nav-bar__sub-menu {
  margin-bottom: 0;
  list-style: none;
  padding: 0;
  font-size: 0.9em;
  background: $sub-menu--bg;
  width: 100%;
  display: none;
  @include breakpoint(large) {
    display: block;
    min-width: 200px;
    width: auto;
    left: 50%;
    white-space: nowrap;
    top: 100%;
    visibility: hidden;
    opacity: 0;
    position: absolute;
    border-radius: 5px;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
    @include translate(-50%, 25px);
    @include transition();
  }
  .nav-bar__menu-link {
    display: flex;
    padding: 3px 12px;
  }
  .nav-bar__menu-item {
    line-height: 2;
    + .nav-bar__menu-item {
      border-top: 1px solid lighten($color-secondary, 10);
    }
  }
  .nav-bar__menu-item + .nav-bar__menu-item {
    &:before {
      display: none;
    }
  }
  .nav-bar__menu-link {
    &:hover {
      background-color: lighten($color-secondary, 10);
      color: #fff;
    }
  }
  &::before {
    @include breakpoint(large) {
      content: '';
      width: 10px;
      height: 10px;
      background: $color-secondary;
      position: absolute;
      top: -5px;
      left: 50%;
      transform: rotate(45deg);
    }
  }
}

.nav-bar__menu-item:hover {
  .nav-bar__sub-menu {
    display: block;
    @include breakpoint(large) {
      visibility: visible;
      opacity: 1;
      @include translate(-50%, 15px);
    }
  }
}
.nav-bar__secondary-menu {
  @supports (margin-inline-start: auto) {
    margin-inline-start: auto;
  }
  @supports not (margin-inline-start: auto) {
    margin-#{$start}: auto;
  }
  @include breakpoint(isCommon) {
    display: none;
  }
}

@include breakpoint(isCommon) {
  .nav-bar--responsive {
    background: url('#{$img-path}/logo-watermark.png') no-repeat left center
      $color-primary;
    z-index: 100;
    width: 75%;
    max-width: 500px;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    padding: 40px 0;
    box-shadow: 2px 0 12px 4px rgba(0, 0, 0, 0.1);
    transform: translateX(-110%);
    @include transition();
    .nav-bar__primary-menu {
      // justify-content: baseline;
      padding: 0;
      display: block;
      font-size: 25px;
    }
    .nav-bar__menu-link {
      border-bottom: 1px solid rgba(#fff, 0.2);
      padding: 15px;
    }
    .nav-bar__sub-menu {
      .nav-bar__menu-link {
        padding: 8px;
      }
    }
  }
  .nav-bar--responsive .nav-bar__menu {
    flex-direction: column;
  }
}

.nav-bar__menu-link--lg {
  font-size: rem(20);
}
